.dashboard {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 42px;
  background-color: #1A1A1A;
  color: white;
}

.right-elements {
  display: flex;
  align-items: center;
}

.left-elements {
  display: flex;
  text-align: center;
  align-items: center;
}

.img {
  width: 21px;
  height: 12.9px;
  top: 15px;
  left: 90px;
  border: 0.4px solid #191919;
  padding-left: 10px;
}

.text {
  width: 76px;
  height: 18px;
  top: 13px;
  left: 118px;
  font-family: Poppins;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: #626262;
  padding-left: 10px;
}

