@media screen and (max-width: 767px) {
  .left-elements {
    flex-direction: row; 
    align-items: center;
  }
  
  .right-elements {
    order: -1; 
    text-align: center;
  }

  .header {
    height: 60px;
    font-size: 14px;
  }

  .text {
    font-size: 10px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .left-elements {
    flex-direction: row; 
    align-items: center;
  }
  
  .right-elements {
    order: -1; 
    text-align: center;
  }

  .header {
    height: 60px;
